@import url("../../utils/base.css");

.input-div,
.radio-input-div {
    width: 100%;
    height: 3.6rem;
    border: 1px solid var(--dark-grey);
    border-radius: 10px;
}

.input-div {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.input-meta {
    display: flex !important;
    flex-direction: row !important;
    justify-content: space-between;
}

.input-meta span {
    color: #e50914;
}

.radio-input-div {
    align-content: center;
    align-items: center;
    padding-left: 1rem;
}

.radio-input-div label {
    display: inline-block;
}

.radio-input-div:focus-within {
    border: 1px solid #d87c4a;
}

input {
    width: 100%;
    padding: 1rem;
    outline: none;
    border: none;
    background-color: rgba(0, 0, 0, 0);
}

input[type="radio"] {
    display: inline;
    position: relative;
    height: 15px;
    width: 15px;
}

input[type="radio"]:checked:before {
    content: "";
    width: 11px;
    height: 11px;
    position: absolute;
    background-color: var(--dark-orange);
    border-radius: 50%;
    top: 2px;
    left: 2px;
}
